.exercises-box {
    background-color: #e8e8e8;
    border-radius: 50px;
    padding: 40px 20px;
    width: 335px;
    margin-left: auto;
    margin-right: auto;
}

.exercises__title {
    color: #1b1b1b;
    font-size: 32px;
    font-weight: 400;
    line-height: 1;
    letter-spacing: -0.64px;
    margin-bottom: 20px;
}

.favorites-exercises__list {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.favorites-info {
    padding-top: 60px;
    margin-left: auto;
    margin-right: auto;
    width: 247px;
}

.favorites-info-text {
    color: #1B1B1B;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.28;
    margin-top: 28px;
}

.favorites-info-img {
    width: 115px;
    height: 71px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width: 768px) {
    .exercises-box {
        padding: 64px 48px;
        width: 736px;
    }

    .exercises__title {
        font-size: 44px;
        line-height: 1.09;
        letter-spacing: -0.88px;
        margin-bottom: 32px;
    }

    .favorites-exercises__list {
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: 14px;
    }

    .favorites-info {
        padding-top: 176px;
        margin-left: auto;
        margin-right: auto;
        width: 554px;
    }

    .favorites-info-text {
        font-size: 18px;
        line-height: 1.33;
    }
}

@media screen and (min-width: 1440px) {
    .exercises-box {
        width: 1408px;
    }

    .favorites-exercises__list {
        row-gap: 28px;
        column-gap: 20px;
    }

    .favorites-info {
        padding-top: 114px;
        margin-left: auto;
        margin-right: auto;
    }
}